import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'
import HomeSearch from '@/pages/search/Search'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/city',
      name: 'City',
      component: City
    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/search',
      name: 'HomeSearch',
      component: HomeSearch
    }
  ],
  // 每次做路由切换时，命x轴y轴都为0
  scrollBehavior(to ,from,savedPosition){
    return {x:0,y:0}
  }
})



