<template>
	<transition>
		<slot></slot><!-- 代表的就是CommonGallary -->
	</transition>
</template>
<script>
	export default{
		name:'Fade'
	};
</script>
<style scoped>
	.v-enter, .v-leave-to{
		opacity: 0
	}
	.v-enter-active, .v-leave-active{
		transition: opacity .5s
	}
</style>


