<template>
	<div class="list" ref='wrapper'>
		<div>
			<div class="area">
				<div class="title border-topbottom">当前城市</div>
				<div class="button-list">
					<div class="button-wrapper">
						<div class="button">{{this.currentCity}}</div>
					</div>
				</div>
			</div>

			<div class="area">
				<div class="title border-topbottom">热门城市</div>
				<div class="button-list">
					<div class="button-wrapper" v-for='item in hotCity' :key='item.id' @click='handleCityClick(item.name)'>
						<div class="button">{{item.name}}</div>
					</div>
				</div>
			</div>

			<div class="area" v-for='(item,key) in cities' :key='key' :ref='key'>
				<div class="title border-topbottom">{{key}}</div>
				<div class="item-list">
					<div class="item border-bottom" v-for='innerItem in item' :key='innerItem.id' @click='handleCityClick(innerItem.name)'>{{innerItem.name}}</div>
				</div>
			</div>

		</div>
	</div>
</template>
<script>
// let wrapper = document.querySelector('.wrapper')
// let scroll = new BScroll(wrapper)
import {mapState,mapMutations} from 'vuex'
import BScroll from 'better-scroll'
	export default{
		props:{
			cities:Object,
			hotCity:Array,
			letter:String
		},
		name:'CityList',
		// 页面dom挂载完毕执行
		mounted(){
			this.scroll = new BScroll(this.$refs.wrapper,{
			click:true,
			tap:true
			})
		},
		watch:{
			letter(){
				if(this.letter){
					const element = this.$refs[this.letter][0]
					//自动滚到某一个元素上
					// 获取的element是一个数组,不是一个dom
					this.scroll.scrollToElement(element)
				}
			}
		},
		computed:{
			// city(){
			// 	return this.$store.state.city
			// }
			//把vuex里面的city这个公用的数据映射到名字为currentCity的计算属性里
			...mapState({
				currentCity:'city'
			})
		},
		methods:{
			handleCityClick(city){
				//触发store里面的actions
				// this.$store.dispatch('changeCity',city)
				// 因为没有异步操作，可以直接使用
				// this.$store.commit('changeCity',city)
				this.changeCity(city)
				this.$router.push('/')
			},
			//有一个mutation叫做changeCity然后把这个mutation映射到我这个组件一个名字为changeCity的方法里
			...mapMutations(['changeCity'])
		}

	};
</script>
<style lang="stylus" scoped>
	.border-topbottom
		&:before
			border-color:#ccc
		&:after
			border-color:#ccc
	.border-bottom
		&:before
			border-color:#ccc	
	.list
		overflow:hidden
		position:absolute
		top:1.58rem
		left:0
		right:0
		bottom:0
		.title
			line-height:.54rem
			background:#eee
			padding-left:.2rem
			font-size:.26rem
			color:#666
		.button-list
			overflow:hidden
			padding:.1rem .6rem .1rem .1rem
			.button-wrapper
				float:left
				margin:.1rem .1rem
				width:30%
				.button
					padding:.1rem
					text-align:.1rem
					text-align:center
					border:.02rem solid #ccc
					border-radius:.06rem
		.item-list
			.item
				line-height:.76rem
				padding-left:.2rem
</style>


