<template>
	<div class="header">
		<div class="header-left">
			<div class="iconfont back-icon">&#xe624;</div>
		</div>
		<router-link tag='div' class="header-input" to='/search'>
			<span class="iconfont">&#xe632;</span>
			输入城市/景点/游玩主题
	    </router-link>
	    <router-link to='/city'>
	    	<div class="header-right">{{this.city}}
				<span class="iconfont arrow-icon">&#xe7c1;</span>
			</div>
	    </router-link>
		
	</div>
</template>
<script>
import { mapState } from 'vuex'
	export default{
		name:'Header',
		computed:{
			// city(){
			// 	return this.$store.state.city
			// },
			//把state里边的值映射到名字叫city的计算属性中
			...mapState(['city'])
		}
	};

</script>
<!-- 移动端的布局一般采用rem的布局方式 -->
<style scoped>
/*@import '~@/assets/styles/varibles.styl'*/
	.header{
		line-height:.86rem;
		display:flex;
		background: #00bcd4;
		color: #fff
	}
	.header-left{
		width:.64rem;
		float:left;
	}
	.back-icon{
		text-align: center;
		font-size: .4rem;
	}
	.header-input{
		flex:1;
		background: #fff;
		height: .64rem;
		border-radius: .1rem;
		margin-top: .12rem;
		padding-left: .2rem;
		margin-left: .2rem;
		line-height: .64rem;
		color: #ccc;

	}
	.header-right{
		min-width: 1.04rem;
		padding: 0 .1rem;
		float: right;
		text-align: center;
		color: #fff
	}
	.arrow-icon{
		margin-left: -.04rem;
		font-size: .24rem;

	}
			

</style>


