<template>
	<ul class="list">
		<li 
			class="item" 
			v-for='item in letters' 
			:key='item' 
			:ref='item'
			@click='handleLetterClick' 
			@touchstart.prevent='handleTouchStart' 
			@touchmove='handleTouchMove' 
			@touchend='handleTouchEnd'>{{item}}
		</li>
	</ul>
</template>
<script>
	export default{
		props:{
			cities:Object
		},
		data(){
			return{
				touchStatus: false,
				startY:0,
				timer:null
			}
		},
		//初次渲染是用一个空，当al渲染出来才执行
		updated(){
			this.startY = this.$refs['A'][0].offsetTop
		},
		computed:{
			letters(){
				const letters =[]
				for(let i in this.cities){
					letters.push(i)
				}
				return letters
				// ['A','B','C']
			}
		},
		name:'CityAlphabet',
		methods:{
			handleLetterClick(e){
				//向外触发事件，事件名字和携带的内容
				this.$emit('change',e.target.innerText)
				// console.log(e.target.innerText)
			},
			handleTouchStart(){
				this.touchStatus = true
			},
			handleTouchMove(e){
				if (this.touchStatus) {
					if(this.timer){
						clearTimeout(this.timer)
					}
					// 如果你已经正在做这件事的时候，让他延迟16ms再执行，假设16ms之间你又执行了手指的滚动，那么他会把上一次的操作给清除掉，重新执行你这一次要做的事情，通过节流大大减少handleTouchMove的执行频率，提高网页的性能
					this.timer = setTimeout(()=>{
						const touchY = e.touches[0].clientY - 74
						//字母的下标，当前手指活动的位置对应的字母的下标
						const index = Math.floor((touchY-this.startY)/20)
						if(index>=0 && index<this.letters.length){
						this.$emit('change',this.letters[index])
					}
					},16)
				}
			},
			handleTouchEnd(){
				this.touchStatus = false
			}
		}
	};
</script>
<style lang="stylus" scoped>
	.list
		display:flex
		flex-direction:column
		justify-content:center
		position:absolute
		top:1.58rem
		right:0
		bottom:0
		width:.4rem
		.item
			line-height:.4rem
			text-align:center
			color:#00bcd4
</style>


