<template>
	<div class="wrapper">
		<swiper :options="swiperOption" v-if="showSwiper">
		    <!-- slides -->
		    <swiper-slide v-for='item in list' :key='item.id'>
		    	<img class="swiper-img" :src="item.imgUrl">
		    </swiper-slide>
		    <!-- Optional controls -->
		    <div class="swiper-pagination"  slot="pagination"></div>
  		</swiper>
	</div>
	
</template>
<script >
	export default{
		name:'HomeSwiper',
		data(){
			return{
				swiperOption:{
					pagination:'.swiper-pagination',
					loop:true,
					autoplay: true,
          			speed: 2000,
				},
			}
		},
		props:{
			list:Array
		},
		computed:{
			showSwiper(){
				return this.list.length
			}
		}
	};
</script>
<style lang='stylus' scoped>
.wrapper>>> .swiper-pagination-bullet-active
		background:#fff
.wrapper
	overfloat:hidden
	width:100%
	height:0
	/*height：31.25vm*/
	padding-bottom:27%
	/*宽高比保持31.25%*/
	.swiper-img
		width:100%

</style>


