<template>
	<div>
		<div class="search">
			<input class="search-input" type="text" placeholder="输入城市名或拼音" v-model='msg'>
		</div>
		<div class="search-content" ref='search' v-show='msg'>
			<ul>
				<li class="search-item border-bottom" v-for='item in list' :key='item.id' @click='handleChangeCity(item.name)'>{{item.name}}</li>
				<li class="search-item border-bottom" v-show='hasNoDate'>
					没有找到匹配数据
				</li>
			</ul>
		</div>	
	</div>
</template>
<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
	export default{
		name:'CitySearch',
		data(){
			return{
				msg:'',
				list:[],
				timer:null
			}
		},
		props:{
			cities:Object
		},
		computed:{
			hasNoDate(){
				return !this.list.length
			}
		},
		watch:{
			msg(){
				if(this.timer){
					clearTimeout(this.timer)
				}
				if(!this.msg){
					this.list=[]
					return
				}
				this.timer = setTimeout(()=>{
					const result =[]
					for(let i in this.cities){
						this.cities[i].forEach((value)=>{
							if(value.spell.indexOf(this.msg)>-1||value.name.indexOf(this.msg)>-1){
								result.push(value)
							}
						})
					}
					this.list = result
				},100)
			}
		},
		mounted(){
			this.BScroll = new BScroll(this.$refs.search)
		},
		methods:{
			handleChangeCity(city){
				// this.$store.commit('changeCity',city)
				// this.$router.push('/')
				this.changeCity(city)
				this.$router.push('/')
			},
			...mapMutations(['changeCity'])
		}
	};
</script>
<style lang='stylus' scoped>
	.search
		height:.72rem
		padding:0 .1rem
		background:#00bcd4
		.search-input
			box-sizing:border-box
			width:100%
			height:.62rem
			padding:0 .1rem
			line-height:.62rem
			text-align:center
			border-radius:.06rem
			color:#666
	.search-content
		z-index:1
		overflow:hidden
		position:absolute
		top:1.58rem
		left:0
		bottom:0
		right:0
		background:#eee
		.search-item
			line-height:.62rem
			padding-left:.2rem
			color:#666
			background:#fff

</style>


