<template>
  <div id="app">
  	<!-- 我的路由被加载过一次之后，我就把路由的内容放到内存之中 -->
  	<!-- 下一次再进去这个路由的时候不需要重新渲染组件，只需要在内存中拿过来就可以了 -->
  	<keep-alive exclude="Detail">
  		<!-- 除去Detail组件，Detail可以多次请求，不被缓存，不受keep-alive的影响 -->
  		<router-view/>
  	</keep-alive>
    
  </div>
</template>

<script>
export default {
  name: 'App'
};
</script>

<style>

</style>



