<template>
	<div class="container" @click='handleCloseGallary'>
		<div class="wrapper">
			<swiper :options='swiperOptions'>
			    <swiper-slide v-for='(item,index) in img' :key='index'>
			    	<img class="gallary-img" :src="item">
			    </swiper-slide>
			    <div class="swiper-pagination" slot="pagination">
			    </div>
  			</swiper>
		</div>
	</div>
</template>
<script>
	export default{
		name:'CommonGallary',
		props:{
			img:{
				type:Array,
				default(){
					return []
				}
			}
		},
		data(){
			return{
				swiperOptions:{
					pagination:'.swiper-pagination',
					paginationType:'fraction',
					//swiper监听到本元素或者父级元素发生了dom结构变化的时候，会自动自我刷新
					observeParents:true,
					observer:true
				}
			}
		},
		methods:{
			//当点击container触发close事件
			handleCloseGallary(){
				this.$emit('close')
			}
		}
	};
</script>
<style lang="stylus" scoped>
	.container >>> .swiper-container
		overflow: inherit
	.container
		display:flex
		flex-direction:column
		justify-content:center
		z-index:99
		position:fixed
		left:0
		right:0
		top:0
		bottom:0
		background:#000
		.wrapper
			height:0
			width:100%
			padding-bottom:100%
			.gallary-img
				width:100%
			.swiper-pagination
				color:#fff
				bottom:-1rem
</style>


