<template>
	<div class="icons">
		<swiper :options="swiperOption" v-if='showSwiper'>
		<swiper-slide v-for='(page,index) in pages' :key='index'>
			<div class="icon" v-for='item in page' :key='item.id'>
				<div class="icon-img">
					<img class="icon-img-content" :src="item.imgUrl">	
				</div>
				<p class="icon-dsc">{{item.desc}}</p>
			</div>
			<div class="swiper-pagination"  slot="pagination"></div>
		</swiper-slide>
		</swiper>

		
	</div>
</template>
<script>
	export default{
		name:'HomeIcons',
		data(){
			return{
				swiperOption:{
					pagination:'.swiper-pagination',
					loop:true,
				},
			}
		},
		props:{
			list:Array
		},
		computed:{
			pages(){
				const pages =[]
				this.list.forEach((item,index)=>{
					const page = Math.floor(index / 8)
					if(!pages[page]){
						pages[page]=[]
					}
					pages[page].push(item)
				})
				return pages
			},
			showSwiper(){
				return this.list.length
			}
		}
	};
</script>
<style lang="stylus" scoped>
.icons >>> .swiper-container
	height:0
	padding-bottom:50%
.icons
	margin-top:.1rem
.icon
	position:relative
	float:left
	width:25%
	height:0
	padding-bottom:25%
	overflow:hidden
	.icon-img
		position:absolute
		top:0
		left:0
		right:0
		bottom:.44rem
		border-sizing:border-box
		padding:.1rem
		.icon-img-content
			height:100%
			display:block
			margin:0 auto
	.icon-dsc
		padding:0
		position:absolute
		left:0
		right:0
		bottom:0
		height:.44rem
		line-height:.44rem
		color:#333
		text-align:center
		overflow:hidden
		white-space:nowrap
		text-overflow:ellipsis
</style>


