<template>
	<div class="header border-bottom">
		<div class="header-left">
			<router-link tag='div' class="iconfont back-icon" to='/'>&#xe624;</router-link>
		</div>
		<div class="header-input">
			<input  type="text" @focus='getFocus' @blur='loseFocus' class="input-content" placeholder="广州长隆低至119元" v-model='inputData'>
			<div class="iconfont cancel-icon" v-if='this.inputData.length' @click='cancelInput'>&#xe64c;</div>
	    </div>
	    <div>
	    	<div class="header-right">搜索</div>
	    </div>
	</div>
</template>
<script>
	export default{
		name:'SearchHeader',
		data(){
			return {
				inputData:''
			}
		},
		methods:{
			cancelInput(){
				this.inputData = ''
			},
			getFocus(){
				this.$store.commit('changeIsShow')
			},
			loseFocus(event){' '
				this.$store.commit('changeIsShow')
			}
		},
	};
</script>
<style scoped>
	.header{
		line-height:.86rem;
		display:flex;
		align-items: center;
		color: #fff;
	}
	.border-bottom{
		border-bottom: 1px solid #eaeaea;
		border-bottom-width: 3px;
		border-bottom-style:solid;
		border-bottom-color: rgb(234, 234, 234);
	}
	.header-left{
		text-align: center;
		width:.65rem;
	}
	.back-icon{
		font-size: .36rem;
		color: #000
	}
	.header-input{
		flex:1;
		display: flex;
		background: #F2F2F2;
		height: .58rem;
		border-radius: 2rem;
	}
	.input-content{
		flex: 9;
		background: #F2F2F2;
		border-radius: .3rem;
		margin-left: .2rem;
		margin-right: .05rem;
		text-align:center;
		line-height: .58rem
	}
	.cancel-icon{
		flex: 1;
		line-height: .58rem;
		color:#BDBDBD;

	}
	.header-right{
		min-width: 1.04rem;
		float: right;
		text-align: center;
		color: #000
	}
</style>


