<template>
	<div>
		<CityHeader></CityHeader>
		<CitySearch :cities='city'></CitySearch>
		<CityList :cities='city' :hotCity='hotCities' :letter='letter'></CityList>
		<CityAlphabet :cities='city' @change='handleLetterClick'></CityAlphabet>
	</div>
</template>
<script>
import CityHeader from '@/pages/city/components/Header'
import CitySearch from '@/pages/city/components/Search'
import CityList from '@/pages/city/components/List'
import CityAlphabet from '@/pages/city/components/Alphabet'
import axios from 'axios'
	export default{
		name:'City',
		components:{
			CityHeader,
			CitySearch,
			CityList,
			CityAlphabet
		},
		data(){
			return{
				city:{},
				hotCities:[],
				letter:''
			}
		},
		methods:{
			getCityInfo(){
				axios.get('https://www.easy-mock.com/mock/5d651778088dcd1da3a07640/example/city')
					.then(this.handleGetCityInfoSucc)
			},
			handleGetCityInfoSucc(res){
				res = res.data
				if(res.ret&&res.data){
					this.city=res.data.cities
					this.hotCities=res.data.hotCities
				}
			},
			handleLetterClick(letter){
				this.letter=letter
				// console.log(letter)
			}
		},
		mounted(){
			this.getCityInfo()
		}
	};
</script>
<style lang='stylus' scoped>
	
</style>


