<template>
	<div>
		<DetailBanner 
			:sightName='sightName' 
			:gallaryImgs='gallaryImgs' 
			:bannerImg='bannerImg'
		>
		</DetailBanner>
		<DetailHeader></DetailHeader>
		<div class="content">
			<DetailList :list='list'></DetailList>
		</div>
	</div>
</template>
<script>
import DetailBanner from './components/Banner'
import DetailHeader from './components/Header'
import DetailList from './components/List'
import axios from 'axios'
	export default{
		name:'Detail',
		components:{
			DetailBanner,
			DetailHeader,
			DetailList
		},
		data(){
			return{
				bannerImg:'',
				sightName:'',
				gallaryImgs:[],
				list:[]
			}
		},
		mounted(){
			this.getDetailInfo()
		},
		methods:{
			getDetailInfo(){
			axios.get('https://www.easy-mock.com/mock/5d651778088dcd1da3a07640/example/detail',{
					params:{
						id:this.$route.params.id
					}
				}).then(this.handleDetDataSucc)
			},
			handleDetDataSucc(res){
				res = res.data
				if(res.ret&&res.data){
					const data = res.data
					this.sightName = data.sightName
					this.bannerImg = data.bannerImg
					this.gallaryImgs = data.gallaryImgs
					this.list = data.categoryList
				}
			}
		},
		
	};
</script>
<style lang="stylus" scoped>
	.content
		height:50rem
</style>


