<template>
	<div>
		<div class="banner" @click='handleShowGallary'>
			<img class="banner-img" :src="bannerImg">
			<div class="banner-info">
				<div class="banner-title">{{this.sightName}}</div>
				<div class="banner-number">
					<span class='iconfont banner-icon'>&#xe600;</span>{{this.gallaryImgs.length}}</div>
			</div>
		</div>
		<Fade>
			<CommonGallary :img='gallaryImgs' v-show='showGallary' @close='handleGallaryClose'>
			</CommonGallary>
		</Fade>
	</div>
</template>
<script>
import CommonGallary from '../../../common/gallary/Gallary'
import Fade from '../../../common/fade/Fade'
	export default{
		name:'DetailBanner',
		props:{
			sightName:String,
			gallaryImgs:Array,
			bannerImg:String
		},
		data(){
			return{
				showGallary:false
			}
		},
		components:{
			CommonGallary,
			Fade
		},
		methods:{
			handleShowGallary(){
				this.showGallary = true
			},
			handleGallaryClose(){
				this.showGallary = false
			}
		}
	};
</script>
<style lang="stylus" scoped>
	.banner
		position:relative
		overflow:hidden
		height:0
		padding-bottom:55%
		.banner-img
			width:100%
		.banner-info
			display:flex
			position:absolute
			left:0
			right:0
			bottom:0
			line-height:.6rem
			color:#fff
			background-image:linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,.8))
			.banner-title
				flex:1
				font-size:.32rem
				padding:0 .2rem
			.banner-number
				height:.32rem
				margin-top:.14rem
				padding:0 .4rem
				line-height:.32rem
				border-radius:.2rem
				background:rgba(0,0,0,.8)
				font-size:.24rem
				.banner-icon
					font-size:.24rem
</style>


