<template>
	<div>
		<router-link tag='div' class="header-abs" to='/' v-show='showAbs'>
			<div class='iconfont header-abs-back'>&#xe624;</div>
		</router-link>
		<div class="header-fixed" v-show='!showAbs' :style='opacityStyle'>
			<router-link to='/'>
				<div class='iconfont header-fixed-back'>&#xe624;</div>
			</router-link>
			景点详情
		</div>
	</div>
</template>
<script>
	export default{
		name:'DeatailHeader',
		data(){
			return{
				showAbs:true,
				opacityStyle:{
					opacity:0
				}
			}
		},
		methods:{
			handleScroll(){
				//获取距离
				// console.log('scroll')
				const top = document.documentElement.scrollTop
				if(top > 50){
					let opacity = top/140
				    opacity = opacity>1? 1:opacity
					this.opacityStyle ={opacity}
					this.showAbs = false
				}else{
					this.showAbs = true
				}
				
				// console.log(document.documentElement.scrollTop)
			}
		},
		activated(){
			//注意  这里绑定了全局的组件，当你在别的组件滚动时也会不断执行，很严重
			window.addEventListener('scroll',this.handleScroll)
		},
		//页面即将被别的页面取代是执行
		//页面展示的时候去绑定scroll事件，页面隐藏的时候去解绑
		deactivated(){
			window.removeEventListener('scroll',this.handleScroll)
		}
	};
</script>
<style lang="stylus" scoped>
	.header-abs
		position:absolute
		left: .2rem
		top:.2rem
		width:.6rem
		height:.6rem
		line-height:.6rem
		text-align:center
		border-radius:.3rem
		background:rgba( 0,0,0,.3)
		.header-abs-back
			color:#fff
			font-size:.4rem
	.header-fixed
		z-index:99
		position:fixed
		top:0
		left:0
		right:0
		height:.86rem
		line-height:.86rem
		text-align:center
		color:#fff
		background:#00bcd4
		font-size:.32rem
		.header-fixed-back
			position:absolute
			top:0
			left:0
			width:.64rem
			text-align:center
			fontsize:.4rem
			color:#fff
</style>


