<template>
	<div>
		<SearchHeader></SearchHeader>
		<HomeSearchTip v-show='isShow'>
		</HomeSearchTip>
	</div>
</template>
<script>
import SearchHeader from '@/pages/search/components/Header'
import HomeSearchTip from '@/pages/search/components/SearchTip'
	export default{
		name:'HomeSearch',
		components:{
			SearchHeader,
			HomeSearchTip
		},
		computed:{
			isShow(){
				return  this.$store.state.isShow
			}
		}
	};
</script>
<style scoped>
</style>


